* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins;
}

.pagination {
  color: #383838;
  background: #fff;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  padding-top: 60px;
  display: flex;
}

ul li {
  color: #000;
  width: 40px;
  height: 40px;
  text-align: center;
  cursor: pointer;
  background: none;
  border-radius: 5px;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 45px;
  display: inline-block;
}

ul li.active {
  color: #fff;
  background: #b92f2c no-repeat;
}

.btn1, .btn2 {
  width: 40px;
  height: 40px;
  cursor: pointer;
  background: #f7f7f7;
  border-radius: 5px;
  outline: none;
  flex-shrink: 0;
}

.btn2 {
  transform: rotate(180deg);
}

.svg {
  height: 100%;
  justify-content: center;
  align-items: center;
}

.btn1:hover, .btn2:hover {
  background: #b92f2c;
}

.link:hover {
  color: #fff;
  background: #b92f2c 0 -45px;
  transition: background-position .5s;
}

